@use "sass:math";@use "sass:string";@use "sass:color";@use "sass:map";@import "_colors"; @import "_variables"; @import "_breakpoints"; @import "_mixins"; // Extra class for specificity to overwrite editor styles.
.wc-block-components-title.wc-block-components-title {
	@include reset-box();
	@include font-size(large);
	word-break: break-word;

	textarea {
		letter-spacing: inherit;
		text-transform: inherit;
		font-weight: inherit;
		font-style: inherit;
	}
}

// For Twenty Twenty we need to increase specificity a bit more.
.theme-twentytwenty {
	.wc-block-components-title.wc-block-components-title {
		@include reset-box();
		@include font-size(large);
		word-break: break-word;
	}
}
